.app {
  display: flex;
  justify-content: space-between;
}

section {
  width: 33%;
  height: 100vh;
  position: relative;
  flex-direction: column;
  border: 1px solid;
  box-sizing: border-box;
  overflow: auto;
  display: flex;
}

h1 {
  background: #fff;
  padding: 10px;
  position: sticky;
  top: 0;
  left: 0;
  margin: 0;
}

.messages {
  flex-grow: 1;
}

.message {
  padding: 5px 10px;
}

.message p {
  margin: 0;
}

.message img {
  width: 100%;
  height: auto;
}

form {
  background: #fff;
  padding: 10px;
  display: flex;
  position: sticky;
  bottom: 0;
  left: 0;
}

input {
  flex-grow: 1;
  border: 1px solid;
}

button {
  margin-left: 10px;
  border: 1px solid;
  border-radius: 0;
}

.input__file {
  position: absolute;
  left: -9999em;
}
